const CACHE_NAME = 'giphy-cache';
const CACHE_DURATION = 1000 * 60 * 15;

// Ï∫êÏãúÏóêÏÑú Îç∞Ïù¥ÌÑ∞Î•º Í∞ÄÏ†∏Ïò§Í≥† ÎßåÎ£å ÏãúÍ∞ÑÏùÑ ÌôïÏù∏ÌïòÎäî Ìï®Ïàò
export const getCachedData = async <T>(key: string): Promise<T | null> => {
  const cache = await caches.open(CACHE_NAME);
  const cachedResponse = await cache.match(key);

  if (!cachedResponse) return null;

  const data = await cachedResponse.json();
  const { timestamp, cachedData } = data;

  // Ï∫êÏãúÍ∞Ä Ïú†Ìö®ÌïúÏßÄ ÌôïÏù∏
  const isCacheValid = Date.now() - timestamp < CACHE_DURATION;
  if (isCacheValid) {
    return cachedData as T; // Ï†úÎÑ§Î¶≠ ÌÉÄÏûÖÏúºÎ°ú Î∞òÌôò
  } else {
    await cache.delete(key);
    return null;
  }
};

// Îç∞Ïù¥ÌÑ∞Î•º Ï∫êÏãúÏóê Ï†ÄÏû•ÌïòÎäî Ìï®Ïàò
export const cacheData = async <T>(key: string, data: T): Promise<void> => {
  const cache = await caches.open(CACHE_NAME);
  const dataToCache = {
    timestamp: Date.now(),
    cachedData: data
  };
  const response = new Response(JSON.stringify(dataToCache));
  await cache.put(key, response);
};

export const getCacheDataOrFetch = async <T>(
  key: string,
  fetchCallback: () => Promise<T>
): Promise<T> => {
  const cachedData = await getCachedData<T>(key);

  if (cachedData) return cachedData;

  const freshData = await fetchCallback();

  await cacheData(key, freshData);

  return freshData;
};
